import { Component, Event, EventEmitter, Prop, Watch, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';

/**
 * @slot label - For the chip label
 * @slot icon - For the chip Icon
 */
@Component({
  tag: 'mdx-chip',
  styleUrl: 'chip.scss',
  shadow: true,
})
export class Chip {
  @Event()
  toggle: EventEmitter<{ selected: boolean; chipId: string }>;

  @Prop({ reflect: true })
  selected = false;

  @Prop({ reflect: true })
  disabled = false;

  @Prop()
  chipId: string;

  @Prop()
  singleSelect?: boolean;

  @Watch('selected')
  selectedChanged(selected: boolean) {
    this.toggle.emit({ selected, chipId: this.chipId });
  }

  render() {
    return (
      <button
        class="chip"
        disabled={this.disabled}
        onClick={(e) => {
          e.preventDefault();
          if (!this.disabled) {
            this.selected = !this.selected;
          }
        }}
      >
        <slot name="icon" />
        <slot name="label" />
        {this.selected && !this.singleSelect ? <mdx-icon-x size={ICON_SIZES.XS} /> : null}
      </button>
    );
  }
}
